@import url(https://fonts.googleapis.com/css?family=PT+Serif+Caption:400,400i&display=swap);
.gridContainer {
    display: block;
    width: 100%;
    height: 25vh;
    max-width: 100%;
}
.Grid {
    background: #fff;
    margin: 1em 0;
    border: 1px solid rgba(34, 36, 38, 0.15);
    box-shadow: none;
    border-radius: 0.28571429rem;
    text-align: left;
    color: rgba(0, 0, 0, 0.87);
    border-collapse: separate;
    border-spacing: 0;
    border-top: 1px solid rgba(34, 36, 38, 0.4);
}

.GridItem {
    display: block;
    font-size: 14px;
    padding: 6px;
    line-height: 1.375;
    font-weight: normal;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border-top: 1px solid rgba(34, 36, 38, 0.1);
}

.GridItemEven {
    background-color: #fff;
}

.GridItemOdd {
    background-color: rgba(0, 0, 50, 0.02);
}

.urlShortName {
    color: #4183c4 !important;
}

.tableContainer {
    display: block;
    max-width: 100%;
    max-height: 100%;
    min-height: 100%;
}
.tableWrapper {
    display: block;
    max-width: 100%;
    max-height: 100%;
    overflow: auto;
}
/*we need this override to make a sortable table header wrap*/
.tableWrapper > .ui.sortable.table thead th {
    white-space: unset !important;
}
/*we need this override to prevent double bordering on the dropdown in pagination menu*/
.tableWrapper .ui.selection.dropdown {
    border: none !important;
}
/*we need this override to properly position the dropdown icon in pagination menu*/
.tableWrapper .ui.selection.dropdown > .dropdown.icon {
    top: auto !important;
}

.tableWrapper .ui.left.floated.menu a {
    color: black;
}

.listContainer {
    display: block;
    width: 100%;
    height: 100%;
}
.List {
    background: #fff;
    border: 1px solid rgba(34, 36, 38, 0.15);
    box-shadow: none;
    border-radius: 0.28571429rem;
    text-align: left;
    color: rgba(0, 0, 0, 0.87);
    border-collapse: separate;
    border-spacing: 0;
}

.ListItemEven,
.ListItemOdd {
    white-space: pre-line;
    display: block;
    font-size: 14px;
    padding: 6px;
    line-height: 1.375;
    font-weight: normal;
    word-break: normal;
    border-top: 1px solid rgba(34, 36, 38, 0.1);
}

.ListItemOdd {
    background-color: rgba(0, 0, 50, 0.02);
}

/* we need to import the font*/

.themeWrapper {
    color: rgba(0, 0, 0, 0.87);
    position: relative;
    z-index: 1;
}

.themeWrapper:after {
    background: inherit;
    bottom: 0;
    content: '';
    display: block;
    height: 50%;
    left: 0;
    position: absolute;
    right: 0;
    transform: skewY(-4deg);
    transform-origin: 100%;
    z-index: -1;
}

.appContainer {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.pageContainer {
    flex: 1 1;
}
.react-grid-item {
    border: 1px solid rgba(34, 36, 38, 0.4);
    border-radius: 0.28571429rem;
}
.internal-grid-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    height: 100%;
}
/* this works because there is always a header before the grid content */
.internal-grid-content {
    /* make sure it always fills the container for height */
    flex: 1 1;
    /* and width */
    width: 100%;
    /*then any overflow gets scrolled*/
    overflow: auto;
}
/* then we want to space out the rows evenly and add some padding for elements with width:100%*/
.internal-grid-content-rows {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
}
/* then we want single rows done differently */
.internal-grid-content-single-row {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}
/*then we multi-child items to spread in single rows */
.internal-grid-content-single-row-spread {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
.ui.progress.success .bar {
    background-color: #058825 !important;
}
.ui.fitted.toggle.checkbox input:checked ~ .box:before,
.ui.fitted.toggle.checkbox input:checked ~ label:before {
    background-color: #1b1c1d !important;
}
.reveal-container {
    height: 25vh;
}
.reveal-visible-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: #f9fafb;
    height: 25vh;
}
.reveal-hidden-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #fff;
    height: 25vh;
    padding: 10px;
}
.reveal-hidden-content-item {
    font-size: 16px;
    font-weight: 700;
}

